import React, { useEffect, useRef, useState } from 'react';
import { message, Spin, Modal } from '@sinohealth/butterfly-ui-antd/lib';
import { QuestionCircleOutlined } from '@ant-design/icons';
import { useNavigate, useSearchParams } from 'react-router-dom';
import moment from 'moment';
import ControlTool from './components/ControlTool';
import DeliveryLog from './components/DeliveryLog';
import DrugList from './components/DrugList';
import OrderStatus from './components/OrderStatus';
import PayInfo from './components/PayInfo';
import PrescriptInfo from './components/PrescriptInfo';
import RationalAdvice from './components/RationalAdvice';
import SideOrder from './components/SideOrder';
import UserInfo from './components/UserInfo';
import RejectedModal from './components/RejectedModal';
import CancelOrReprescModal from './components/CancelOrReprescModal';
import SuccessModal from './components/SuccessModal';
import RedeliveryModal from './components/RedeliveryModal';
import CancelDeliveryModal from './components/CancelDeliveryModal';
import PassedModal from './components/PassedModal';
import StatusChangeModal from './components/StatusChangeModal';
import DrugInstructModal from './components/DrugInstructModal';
import BackBreadcrumb from '@/components/BackBreadcrumb';

import {
  httpCancelDelivery,
  httpDoRePrescribe,
  httpEntryDeliveryInfo,
  httpFinishDelivery,
  httpGetPharmacyOrderDetail,
  httpOrderException,
  httpOrderFinish,
  httpOrderReview,
  httpPharmacyOrderList,
  httpQueryInstruction,
  httpRoDelivery,
  httpSingleDelivery,
} from '@/services/system';
import styles from './index.less';
import { useRationalDrug, useTimeoutTip } from './useDetailHook';
import { useDictHttp } from '@/hooks/useDict';
import useSocket from '@/hooks/useSocket';
import { getCookies, removeCookies, setCookies } from '@/utils/cookies';

const { confirm } = Modal;

const FOOT_CONTROL: any = {
  rejected: true,
  finished: true,
  delivering: true,
};

/**
 * 审方订单详情
 * @returns
 */
const ReviewOrderDetailDetail: React.FC = () => {
  const [isError, setIsError] = useState(false);
  const [params] = useSearchParams();
  const { socket } = useSocket();
  const ids = params.getAll('id')[0];
  const { dictList } = useDictHttp();
  const {
    interaction,
    interactionTip,
    avoid,
    avoidTip,
    beyondIndication,
    repeatMedication,
    repeatMedicationTip,
    remarkCode,
    errorCode,
  } = useRationalDrug(ids, isError);
  const fromType = params.getAll('from')[0];
  const detailType = 'revieOrder'; // orderSearch：订单查询 revieOrder：审方订单
  const selectTab = params.getAll('type')[0];
  const timeRef = useRef<any>(null);
  const navigate = useNavigate();
  const [orderSource, setOrderSource] = useState<AdminOrder.GetListRes[]>([]);
  const [detail, setDetail] = useState<AdminOrder.OrderDetailRes>();
  const [total, setTotal] = useState(0);
  // 切换类型
  const [isChangeTab, setIsChangeTab] = useState(false);
  // 下一单
  // 加载中
  const [loadingSpin, setLoadingSpin] = useState(true);
  // 左侧加载中
  const [isLoading, setIsLoading] = useState(false);
  // 详情超时提示
  const [timeoutTip, setTimeoutTip] = useState<number>(0);
  // 操作成功提示
  const [successTipTit, setSuccessTipTit] = useState('');
  // 审核驳回
  const [isRejectedOrder, setIsRejectedOrder] = useState(false);
  // 取消订单/重新开方
  const [isCancelOrRepresc, setIsCancelOrRepresc] = useState(false);
  // 取消配送
  const [isCancelDelivery, setIsCancelDelivery] = useState(false);
  // 成功提示
  const [isSuccess, setIsSuccess] = useState(false);
  // 重新配送
  const [isRedelivery, setIsRedelivery] = useState(false);
  // 审核通过
  const [isPassed, setIsPassed] = useState(false);
  // 是否稍候配送
  const [laterDelivery, setLaterDelivery] = useState(false);
  // 开始配送或重新配送
  const [startOrAgain, setStartOrAgain] = useState(false);
  const [isStateChange, setIsStateChange] = useState(false);
  const [isDrugInstruct, setIsDrugInstruct] = useState(false);
  const [rationalDrug, setRationalDrug] = useState<any>({});

  const [orderParams, setOrderParams] = useState<AdminOrder.SearchParams>({
    pageNo: 1,
    pageSize: 50,
    entity: {
      selectTab,
    },
    orders: [{ property: 'create_time', direction: 'asc' }],
  });

  const onSocketMessage = () => {
    if (selectTab === 'wait') {
      setOrderParams({
        ...orderParams,
        pageNo: 1,
      });
    }
  };

  // 获取列表数据
  async function httpGetOrdeListReq() {
    setIsLoading(true);
    setLoadingSpin(true);
    try {
      const res: any = await httpPharmacyOrderList(orderParams);
      setTotal(res?.total);
      if (selectTab === 'wait' && res.records.length > 0) {
        res.records.forEach((el: any) => {
          // eslint-disable-next-line no-param-reassign
          el.timeout = useTimeoutTip(el.createTime, el.deliveryCategory);
        });
        console.log(res);
      }

      if (getCookies('ContinueNext')) {
        if (res.records.length > 0) {
          navigate(`/prescrip/order/reviewDetail?id=${res.records[0].id}&type=${selectTab}`);
        } else {
          httpGetOrderDetailReq();
        }
      } else if (isChangeTab && res.records.length > 0) {
        setIsChangeTab(false);
        navigate(`/prescrip/order/reviewDetail?id=${res.records[0].id}&type=${selectTab}`);
      } else {
        httpGetOrderDetailReq(ids);
      }
      setIsLoading(false);
      if (orderParams.pageNo === 1) {
        // eslint-disable-next-line no-unsafe-optional-chaining
        setOrderSource([...res?.records]);
      } else {
        setOrderSource(orderSource.concat(res?.records));
      }
      setLoadingSpin(false);
    } catch (err) {
      setLoadingSpin(false);
    }
  }

  // 订单详情接口
  async function httpGetOrderDetailReq(orderId: string = '') {
    clearTimeout(timeRef.current);
    try {
      const res: AdminOrder.OrderDetailRes = await httpGetPharmacyOrderDetail(orderId || ids);
      setLoadingSpin(false);

      const TYPES: any = {
        O2O: 'minutes',
        B2C: 'hours',
      };
      const TIME: any = {
        O2O: 500,
        B2C: 500,
      };

      if (selectTab === 'wait') {
        timeRef.current = setInterval(() => {
          setTimeoutTip(
            moment(Date.now()).diff(
              res.orderInfo.createTime,
              TYPES[res.orderInfo.deliveryCategory],
            ),
          );
        }, TIME[res.orderInfo.deliveryCategory]);
      }
      setDetail(res);
      setIsChangeTab(false);
      setCookies('ContinueNext', false);
    } catch (err) {
      setLoadingSpin(false);
    }
  }

  // 药品说明书
  async function httpQueryInstructionReq(id: string) {
    try {
      const res: any = await httpQueryInstruction(id);
      // eslint-disable-next-line camelcase
      const { common_name: commonName, content } = res;
      setIsDrugInstruct(true);
      setRationalDrug({ commonName, ...content });
    } catch (err) {
      setIsDrugInstruct(false);
    }
  }

  // 审核
  async function httpOrderReviewReq(par: AdminOrder.OrderReview) {
    setIsPassed(false);
    setIsRejectedOrder(false);
    try {
      const { chainId, id, storeId } = detail?.orderInfo || {};
      const res = await httpOrderReview({ ...par, chainId, orderId: id, storeId });
      if (res.code === 200) {
        const TYPE_TIPS: any = {
          reject: '处方已驳回',
          delivery_wait: '处方审核成功',
          pass_verify: '处方审核成功',
        };
        setIsSuccess(true);
        setSuccessTipTit(TYPE_TIPS[par.verifyStatus]);
      }
    } catch (err) {
      setLoadingSpin(false);
    }
  }

  // 取消配送
  const handleCancelDelivery = async (value: string) => {
    setLoadingSpin(true);
    const { chainId, id, storeId } = detail?.orderInfo || {};
    try {
      const res = await httpCancelDelivery({
        chainId,
        orderId: id,
        clientId: storeId,
        cancelReason: value,
      });

      setIsCancelDelivery(false);
      if (res.code === 200) {
        setIsSuccess(true);
        setSuccessTipTit('订单已取消配送');
      }
    } catch (err: any) {
      setIsCancelDelivery(false);
      if (err?.code === 1000009) {
        setSuccessTipTit('当前订单状态有更新');
        setIsStateChange(true);
      } else {
        setLoadingSpin(false);
      }
    }
  };

  // 模拟异常配送
  const handleDeliveryException = async () => {
    const hide = message.loading('数据正在处理中, 请稍候...', 0);
    try {
      const res = await httpOrderException(ids);
      console.log(res);
      if (res.code === 200) {
        const timer = setTimeout(() => {
          hide();
          clearTimeout(timer);
          setSuccessTipTit('模拟异常配送成功');
          setIsSuccess(true);
        }, 3000);
      } else {
        hide();
        message.error('模拟异常配送失败');
        setLoadingSpin(false);
        httpGetOrderDetailReq();
      }
    } catch (err: any) {
      hide();
      if (err?.code === 1000009) {
        setSuccessTipTit('当前订单状态有更新');
        setIsStateChange(true);
      } else {
        setLoadingSpin(false);
      }
    }
  };

  // 模拟完成订单
  const handleOrderCompletion = async () => {
    const hide = message.loading('数据正在处理中, 请稍候...', 0);
    try {
      const res = await httpOrderFinish(ids);

      if (res.code === 200) {
        const timer = setTimeout(() => {
          hide();
          clearTimeout(timer);
          setSuccessTipTit('模拟完成订单成功');
          setIsSuccess(true);
        }, 3000);
      } else {
        hide();
        message.error('模拟完成订单失败');
        httpGetOrderDetailReq();
      }
    } catch (err: any) {
      hide();
      if (err?.code === 1000009) {
        setSuccessTipTit('当前订单状态有更新');
        setIsStateChange(true);
      } else {
        setLoadingSpin(false);
      }
    }
  };

  // 重新配送
  const handleReDelivery = async () => {
    const { chainId, id, storeId } = detail?.orderInfo || {};
    const hide = message.loading('数据正在处理中, 请稍候...', 0);
    if (!startOrAgain) {
      setLoadingSpin(true);
      setIsRedelivery(false);
      try {
        const res = await httpRoDelivery({ chainId, orderId: id, clientId: storeId });
        console.log(res);
        if (res.code === 200) {
          const timer = setTimeout(() => {
            hide();
            clearTimeout(timer);
            setIsSuccess(true);
            setSuccessTipTit('订单已重新配送');
          }, 3000);
        } else {
          hide();
          setLoadingSpin(false);
        }
      } catch (err: any) {
        hide();
        if (err?.code === 1000009) {
          setSuccessTipTit('当前订单状态有更新');
          setIsStateChange(true);
        } else {
          setLoadingSpin(false);
        }
      }
    } else {
      setIsRedelivery(false);
      setLoadingSpin(true);
      try {
        const res = await httpSingleDelivery({ chainId, orderId: id, clientId: storeId });

        if (res.code === 200) {
          setIsSuccess(true);
          setSuccessTipTit('配送成功');
        }
        hide();
      } catch (err: any) {
        hide();
        setIsRedelivery(false);
        if (err?.code === 1000009) {
          setSuccessTipTit('当前订单状态有更新');
          setIsStateChange(true);
        } else {
          setLoadingSpin(false);
        }
      }
    }
  };

  // 取消订单
  const handleCancelOrder = async (value: string) => {
    setLoadingSpin(true);
    try {
      const { chainId, id, storeId } = detail?.orderInfo || {};
      const res = await httpDoRePrescribe({
        chainId,
        orderId: id,
        clientId: storeId,
        reason: value,
      });

      setIsCancelOrRepresc(false);
      if (res.code === 200) {
        setIsSuccess(true);
        setSuccessTipTit('订单取消成功');
      }
    } catch (err: any) {
      setIsCancelOrRepresc(false);
      if (err?.code === 1000009) {
        setSuccessTipTit('当前订单状态有更新');
        setIsStateChange(true);
      } else {
        setLoadingSpin(false);
      }
    }
  };

  // 成功弹窗-确认
  const handleCloseSuccessModal = () => {
    setIsSuccess(false);
    setCookies('ContinueNext', true);
    httpGetOrdeListReq();
  };

  // 成功弹窗-取消
  const handleCancelSuccessModal = () => {
    setIsSuccess(false);
    setCookies('ContinueNext', false);
    httpGetOrdeListReq();
  };

  const handleStateChange = () => {
    setIsStateChange(false);
    setCookies('ContinueNext', false);
    httpGetOrdeListReq();
  };

  // 手动录单
  const handleEntryDeliver = async (val: AdminOrder.EntryDelivery) => {
    const hide = message.loading('数据正在处理中, 请稍候...', 0);
    setLoadingSpin(true);
    try {
      const res: any = await httpEntryDeliveryInfo({ ...val, orderId: detail?.orderInfo?.id });
      if (res?.code === 200) {
        const timer = setTimeout(() => {
          message.success('录入成功');
          hide();
          setLoadingSpin(false);
          httpGetOrderDetailReq();
          clearTimeout(timer);
        }, 3000);
      } else {
        httpGetOrderDetailReq();
        hide();
        message.warn('录入失败, 请重试');
      }
    } catch (err: any) {
      hide();
      if (err?.code === 1000009) {
        setSuccessTipTit('当前订单状态有更新');
        setIsStateChange(true);
      } else {
        setLoadingSpin(false);
      }
    }
  };
  // 手动完成订单
  const handleFinshDelivery = () => {
    setLoadingSpin(true);
    confirm({
      icon: <QuestionCircleOutlined />,
      content: '是否手动确认收货?',
      centered: true,
      onOk: async () => {
        const hide = message.loading('数据正在处理中, 请稍候...', 0);
        try {
          const res: any = await httpFinishDelivery(detail?.orderInfo?.id);
          if (res.code === 200) {
            const timer = setTimeout(() => {
              hide();
              setIsSuccess(true);
              setSuccessTipTit('配送已完成');
              clearTimeout(timer);
            }, 3000);
          } else {
            hide();
            httpGetOrderDetailReq();
            setLoadingSpin(false);
            message.warn('录入失败, 请重试');
          }
        } catch (err: any) {
          hide();
          if (err?.code === 1000009) {
            setSuccessTipTit('当前订单状态有更新');
            setIsStateChange(true);
          } else {
            setLoadingSpin(false);
          }
        }
      },
      onCancel() {
        console.log('Cancel');
        setLoadingSpin(false);
      },
    });
  };

  useEffect(() => {
    httpGetOrdeListReq();

    return () => {
      removeCookies('ContinueNext');
      clearTimeout(timeRef.current);
    };
  }, [orderParams, ids]);

  useEffect(() => {
    socket?.addEventListener('ADD', onSocketMessage);

    return () => socket?.removeEventListener('ADD', onSocketMessage);
  });

  return (
    <Spin spinning={loadingSpin} tip="数据正在拼命加载中, 请稍候..." delay={500}>
      <div className={styles['order-container']}>
        <SideOrder
          list={orderSource}
          loading={isLoading}
          total={total}
          delivDict={dictList ? dictList.deliveryStatus : {}}
          tab={selectTab}
          formType={detailType}
          height="calc(100vh - 170px)"
          id={ids}
          onScroll={() => {
            if (detailType === 'revieOrder') {
              clearTimeout(timeRef.current);
              setOrderParams({
                ...orderParams,
                pageNo: orderParams.pageNo + 1,
              });
            }
          }}
          onTab={(val) => {
            clearTimeout(timeRef.current);
            setOrderParams({
              ...orderParams,
              pageNo: 1,
              entity: {
                selectTab: val,
              },
            });
            setIsChangeTab(true);
          }}
          onChange={(id) => {
            clearTimeout(timeRef.current);
            // setLoadingSpin(true);
            // httpGetOrderDetailReq(id);
            // window.history.pushState(null, '', `?id=${id}&type=${selectTab}`);
            navigate(`/prescrip/order/reviewDetail?id=${id}&type=${selectTab}`);
          }}
          onSearch={(keyword) => {
            setOrderParams({
              ...orderParams,
              pageNo: 1,
              entity: {
                ...orderParams.entity,
                simpleSearch: keyword,
              },
            });
          }}
        />
        <div className={styles['perscript-container']}>
          {/* 面包屑 */}
          <BackBreadcrumb
            breadcrumbMap={[
              { label: '处方流转' },
              { label: '处方管理' },
              { label: '审方订单', link: '/prescrip/order/list' },
              { label: '订单详情' },
            ]}
            link="/prescrip/order/list"
          />
          {/* 用户信息 */}
          <UserInfo
            clas={styles.mb12}
            patient={detail?.patientInfo}
            delivery={detail?.deliveryInfo}
          />
          <div className={styles.container}>
            <div
              className={`${styles['left-container']} ${
                detail?.currentStatus && FOOT_CONTROL[detail?.currentStatus]
                  ? styles['left-container-calc']
                  : ''
              }`}
            >
              <OrderStatus
                clas={styles.mb12}
                id={detail?.orderInfo?.id}
                tip={timeoutTip}
                processStatus={detail?.processStatus}
                receiveTime={detail?.orderInfo?.receiveTime}
                doctor={detail?.prescriptionInfo?.doctor}
                pCreateTime={detail?.orderInfo?.createTime}
                dispensePharmacist={detail?.prescriptionInfo?.dispensePharmacist}
                createTime={detail?.deliveryInfo?.updateTime}
                auditPharmacist={detail?.prescriptionInfo?.auditPharmacist}
                deliveryUser={detail?.deliveryInfo?.deliveryUser}
                signTime={detail?.deliveryInfo?.signTime}
                sourceTime={detail?.prescriptionInfo?.sourceTime}
                signUser={detail?.deliveryInfo?.signUser}
                currentStatus={detail?.currentStatus}
                message={detail?.orderInfo?.message}
                invalidReason={detail?.orderInfo?.invalidReason}
                deliveryCategory={detail?.orderInfo?.deliveryCategory}
                formType={detailType}
                currentStatusDict={dictList ? dictList.OrderCurrentStatus : {}}
              />
              <DrugList
                clas={styles.mb12}
                totalAmount={detail?.orderInfo?.totalAmount}
                deliveryAmount={detail?.orderInfo?.deliveryAmount}
                drugList={detail?.prescriptionInfo?.drugList}
                currentStatus={detail?.currentStatus}
                drugFrequency={dictList ? dictList.drugFrequency : {}}
                drugUsage={dictList ? dictList.drugUsage : {}}
              />
              <RationalAdvice
                clas={styles.mb12}
                currentStatus={detail?.currentStatus}
                interaction={interaction || []}
                interactionTip={interactionTip}
                avoid={avoid || []}
                avoidTip={avoidTip}
                beyondIndication={beyondIndication || []}
                repeatMedication={repeatMedication || []}
                repeatMedicationTip={repeatMedicationTip}
                remarkCode={remarkCode}
                errorCode={errorCode}
                onDrug={(drugid) => httpQueryInstructionReq(drugid)}
                onReload={() => setIsError(!isError)}
              />
              <DeliveryLog
                receiptInfo={{
                  toName: detail?.deliveryInfo?.toName,
                  toAddress: detail?.deliveryInfo?.toAddress,
                  toPhone: detail?.deliveryInfo?.toPhone,
                  orderId: detail?.deliveryInfo?.orderId,
                }}
                code={detail?.deliveryInfo?.deliveryCode}
                shippInfo={{
                  deliveryCode: detail?.deliveryInfo?.deliveryCode,
                  deliveryNo: detail?.deliveryInfo?.deliveryNo,
                  deliveryStatus: detail?.orderInfo?.deliveryStatus,
                  deliveryUser: detail?.deliveryInfo?.deliveryUser,
                  deliveryPhone: detail?.deliveryInfo?.deliveryPhone,
                  orderId: detail?.deliveryInfo?.orderId,
                  deliverySource: detail?.deliveryInfo?.deliverySource,
                }}
                formType={detailType}
                deliverySupplyer={dictList ? dictList?.deliverySupplyer : {}}
                deliveryStatusDict={dictList ? dictList?.deliveryStatus : {}}
                deliverySource={detail?.deliveryInfo?.deliverySource}
                status={detail?.currentStatus}
                entry={(val) => handleEntryDeliver(val)}
                finsh={() => handleFinshDelivery()}
              />
            </div>
            <div className={styles['perscript-info']}>
              <PayInfo clas={styles.mb12} payInfo={detail?.orderInfo} />
              <PrescriptInfo
                perscript={detail?.prescriptionInfo}
                createTime={detail?.orderInfo?.createTime}
                isCalc={detail?.currentStatus && FOOT_CONTROL[detail?.currentStatus]}
                patientName={detail?.patientInfo?.name}
              />
            </div>
          </div>
          {/* 底部操作栏 */}
          {detail?.currentStatus && FOOT_CONTROL[detail?.currentStatus] ? null : (
            <ControlTool
              formType={detailType}
              type={detail?.orderInfo?.deliveryCategory}
              status={detail?.currentStatus}
              // 审核驳回
              reject={() => {
                setIsRejectedOrder(true);
                setLoadingSpin(true);
              }}
              // 审核通过，开始配送
              passNow={() => {
                setIsPassed(true);
                setLoadingSpin(true);
                setSuccessTipTit('是否审核通过，立即配送');
                setLaterDelivery(false);
              }}
              // 审核通过，稍后配送
              passWait={() => {
                setIsPassed(true);
                setSuccessTipTit('是否审核通过，稍后配送');
                setLoadingSpin(true);
                setLaterDelivery(true);
              }}
              // 重新配送
              reDelivery={() => {
                setLoadingSpin(true);
                setIsRedelivery(true);
                setStartOrAgain(false);
              }}
              // cancelDelivery
              cancelDelivery={() => {
                setIsCancelDelivery(true);
                setLoadingSpin(true);
              }}
              // 模拟配送异常
              exception={() => {
                setLoadingSpin(true);
                handleDeliveryException();
              }}
              // 重新开方
              rePrescribe={() => {
                setLoadingSpin(true);
                setIsCancelOrRepresc(true);
              }}
              // 开始配送
              delivery={() => {
                setLoadingSpin(true);
                setIsRedelivery(true);
                setStartOrAgain(true);
              }}
              // 模拟完成订单
              completion={() => {
                setLoadingSpin(true);
                handleOrderCompletion();
              }}
            />
          )}
        </div>
        {/* 审核驳回 */}
        {isRejectedOrder ? (
          <RejectedModal
            visible={isRejectedOrder}
            drugList={detail?.prescriptionInfo?.drugList}
            onCancel={() => {
              setIsRejectedOrder(false);
              setLoadingSpin(false);
            }}
            onOk={(val) => {
              httpOrderReviewReq({
                verifyStatus: 'reject',
                rejectedReason: val,
              });
            }}
          />
        ) : null}
        {/* 审核通过 */}
        {isPassed ? (
          <PassedModal
            visible={isPassed}
            tipTitle={successTipTit}
            okText="确定"
            onCancel={() => {
              setIsPassed(false);
              setLoadingSpin(false);
            }}
            onOk={() => {
              httpOrderReviewReq({
                verifyStatus: laterDelivery ? 'delivery_wait' : 'pass_verify',
              });
            }}
          />
        ) : null}
        {/* 取消订单/重新开方 */}
        {isCancelOrRepresc ? (
          <CancelOrReprescModal
            visible={isCancelOrRepresc}
            drugList={detail?.prescriptionInfo?.drugList}
            onCancel={() => {
              setIsCancelOrRepresc(false);
              setLoadingSpin(false);
            }}
            onOk={(val) => handleCancelOrder(val)}
          />
        ) : null}
        {/* 取消配送 */}
        {isCancelDelivery ? (
          <CancelDeliveryModal
            visible={isCancelDelivery}
            drugList={detail?.prescriptionInfo?.drugList}
            onCancel={() => {
              setIsCancelDelivery(false);
              setLoadingSpin(false);
            }}
            onOk={(val) => {
              handleCancelDelivery(val);
            }}
          />
        ) : null}
        {/* 重新配送 */}
        {isRedelivery ? (
          <RedeliveryModal
            visible={isRedelivery}
            onCancel={() => {
              setIsRedelivery(false);
              setLoadingSpin(false);
            }}
            onOk={() => handleReDelivery()}
          />
        ) : null}
        {/* 成功提示 */}
        {isSuccess ? (
          <SuccessModal
            visible={isSuccess}
            onCancel={() => handleCancelSuccessModal()}
            okText="好的，下一单"
            onOk={() => handleCloseSuccessModal()}
            tipTitle={successTipTit}
          />
        ) : null}
        {/* 订单状态有更新 */}
        {isStateChange ? (
          <StatusChangeModal
            visible={isStateChange}
            tipTitle={successTipTit}
            onOk={() => handleStateChange()}
            onCancel={() => handleStateChange()}
          />
        ) : null}
        {/* 药品说明书 */}
        <DrugInstructModal
          visible={isDrugInstruct}
          {...rationalDrug}
          onCancel={() => setIsDrugInstruct(false)}
        />
      </div>
    </Spin>
  );
};

export default ReviewOrderDetailDetail;
