import React, { useEffect, useState } from 'react';
import { Radio, Space, Input, Select, message } from '@sinohealth/butterfly-ui-antd/lib';
import DraggableModal from '@/components/DraggableModal';
import styles from './index.less';

const { TextArea } = Input;
const { Option } = Select;

type CancelDeliveryModalProps = {
  visible: boolean;
  orderType?: string;
  drugList?: AdminOrder.DrugList[];
  onCancel: () => void;
  onOk: (val: string) => void;
};

/**
 * 取消订单/重新开方
 * @returns
 */
const CancelDeliveryModal: React.FC<CancelDeliveryModalProps> = (props) => {
  const { visible, orderType, drugList, onCancel, onOk } = props;
  const [currentType, setCurrentType] = useState(0);
  const [selectDrug, setSelectDrug] = useState<string[]>([]);
  const [otherReasons, setOtherReasons] = useState('');

  const reason = () => {
    switch (currentType) {
      case 1:
        return `药品缺失: ${selectDrug?.join(',')}`;
      case 2:
        return '等候超时';
      case 3:
        return `其他: ${otherReasons}`;
      default:
        return '';
    }
  };

  useEffect(() => {
    setCurrentType(0);
    setSelectDrug([]);
    setOtherReasons('');
  }, [visible]);

  return (
    <DraggableModal
      visible={visible}
      closable={false}
      tipTitle="取消配送"
      width="540px"
      orderType={orderType}
      maskClosable={false}
      okButtonProps={{
        type: 'warning',
      }}
      cancelButtonProps={{
        type: 'info',
      }}
      onOk={() => {
        if (currentType === 0) {
          message.error('请选择取消原因');
          return;
        }
        if (currentType === 1) {
          if (!selectDrug.length) {
            message.error('请选择缺失的药品');
            return;
          }
        }
        if (currentType === 3) {
          if (otherReasons.trim() === '') {
            message.error('请填写理由');
            return;
          }
        }
        onOk(reason());
      }}
      onCancel={() => onCancel()}
    >
      <div className={styles['rejected-modal']}>
        <div className={styles['reason-title']}>
          将通知医师调整或重新开方，已支付金额原路退还。取消原因：
        </div>
        <Radio.Group
          value={currentType}
          className={styles['reason-radio']}
          onChange={(e) => setCurrentType(e.target.value)}
        >
          <Space direction="vertical">
            <Radio value={1}>
              <span>药品缺药</span>
            </Radio>
            <Radio value={2}>等候超时</Radio>
            <Radio value={3}>
              <span>其他</span>
            </Radio>
          </Space>
        </Radio.Group>
        <Select
          mode="multiple"
          size="large"
          placeholder="请选择"
          disabled={currentType !== 1}
          className={styles['reason-select']}
          maxTagCount="responsive"
          onChange={(values) => {
            console.log(values);
            setSelectDrug(values);
          }}
        >
          {drugList?.map((el) => (
            <Option value={el?.name} key={el.commonName}>
              {el?.name}
            </Option>
          ))}
        </Select>
        <TextArea
          placeholder="请输入内容"
          disabled={currentType !== 3}
          className={styles['reason-text']}
          autoSize={{ minRows: 4, maxRows: 5 }}
          onChange={(e) => setOtherReasons(e.target.value)}
        />
      </div>
    </DraggableModal>
  );
};

export default CancelDeliveryModal;
