import React from 'react';
import { QuestionCircleFilled } from '@ant-design/icons';
import DraggableModal from '@/components/DraggableModal';

type PassedModalProps = {
  visible: boolean;
  tipTitle: string;
  width?: number;
  okText?: string;
  icon?: any;
  onOk: () => void;
  onCancel: () => void;
};

/**
 * 成功提示弹窗
 * @returns
 */
const PassedModal: React.FC<PassedModalProps> = (props) => {
  const {
    visible,
    tipTitle,
    width = '420px',
    okText = '好的',
    icon = <QuestionCircleFilled style={{ color: '#FFBF00', fontSize: 24 }} />,
    onOk,
    onCancel,
  } = props;

  return (
    <DraggableModal
      visible={visible}
      closable={false}
      tipTitle={tipTitle}
      width={width}
      icon={icon}
      okText={okText}
      maskClosable={false}
      cancelButtonProps={{
        type: 'info',
      }}
      onOk={() => {
        onOk();
      }}
      onCancel={() => onCancel()}
    />
  );
};

export default PassedModal;
