import React from 'react';
import { ExclamationCircleOutlined } from '@ant-design/icons';
import DraggableModal from '@/components/DraggableModal';

type StatusChangeModalProps = {
  visible: boolean;
  tipTitle: string;
  width?: number;
  okText?: string;
  icon?: any;
  onOk: () => void;
  onCancel?: () => void;
};

/**
 * 状态改变弹窗
 * @returns
 */
const StatusChangeModal: React.FC<StatusChangeModalProps> = (props) => {
  const {
    visible,
    tipTitle,
    width = '420px',
    okText = '好的',
    icon = (
      <ExclamationCircleOutlined style={{ color: '#217ba0', marginRight: 16, fontSize: 24 }} />
    ),
    onOk,
    onCancel,
  } = props;

  return (
    <DraggableModal
      visible={visible}
      closable={false}
      tipTitle={tipTitle}
      width={width}
      icon={icon}
      okText={okText}
      cancelText={null}
      maskClosable={false}
      cancelButtonProps={{
        type: 'info',
      }}
      onOk={() => {
        onOk();
      }}
      onCancel={() => onCancel && onCancel()}
    />
  );
};

export default StatusChangeModal;
